<template>
  <div>
    <p class="text-center m-0">
      <a-space class="flex-wrap">
        <a-button type="primary" class="mb-16px" @click="$emit('fire')"> æ¶è </a-button>
        <a-button
          type="primary" class="mb-16px"
          :disabled="!groupRecords.fullTimeRecords.length"
          @click="execFullTime"
        >
          å¨æ¶å¨è
        </a-button>
        <a-button
          type="primary" class="mb-16px"
          :disabled="!groupRecords.partTimeRecords.length"
          @click="execPartTime"
        >
          å¨æ¶å¼è
        </a-button>
        <a-button
          :disabled="!groupRecords.gt2kRecords.length"
          type="primary" class="mb-16px"
          @click="exec2k"
        >
          æ¶2000
        </a-button>
        <a-button type="primary" class="mb-16px" @click="execAll"> å¨æ¶ </a-button>
      </a-space>
    </p>
    <a-table
      bordered
      :loading="loading"
      :columns="tableColumns"
      :data-source="dataSourceWithKey"
      :scroll="{ x: true }"
      :pagination="false"
      :row-selection="{
        selectedRowKeys: selectedRowKeys,
        onChange: onSelectChange,
      }"
    >
      <template slot="tokenId" slot-scope="_, record">
        <div class="flex-center">
          <span :title="record.tokenId">{{
            displayOptimize(record.tokenId)
          }}</span>
          <a-button
            type="primary"
            size="small"
            :id="`btn-${record.key}`"
            :data-clipboard-text="record.tokenId"
            style="margin-left: 10px"
            @click="copyAddr(record.key)"
          >
            å¤å¶
          </a-button>
        </div>
      </template>
      <template slot="workType" slot-scope="_, record">
        <a-tag v-if="record.workType === 1" color="#1890ff">å¨è</a-tag>
        <a-tag v-else-if="record.workType === 2" color="#faad14">å¼è</a-tag>
      </template>
    </a-table>
  </div>
</template>

<script>
import ClipboardJs from "clipboard";
import { genUniqueKey } from "../../utils/index";

export default {
  props: {
    dataSource: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedRowKeys: [],
    };
  },
  computed: {
    tableColumns() {
      return [
        {
          title: "token id",
          scopedSlots: { customRender: "tokenId" },
        },
        {
          title: "å·¥ä½",
          slot: "workType",
          width: 90,
          scopedSlots: { customRender: "workType" },
        },
        {
          title: "èä¸",
          width: 90,
          dataIndex: "career",
        },
        {
          title: "åé",
          width: 90,
          dataIndex: "strength",
          sorter: (a, b) => a.strength - b.strength,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "ææ·",
          width: 90,
          dataIndex: "agility",
          sorter: (a, b) => a.agility - b.agility,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "ä½è´¨",
          width: 90,
          dataIndex: "physique",
          sorter: (a, b) => a.physique - b.physique,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "æå¿",
          width: 90,
          dataIndex: "volition",
          sorter: (a, b) => a.volition - b.volition,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "æºå",
          width: 90,
          dataIndex: "brains",
          sorter: (a, b) => a.brains - b.brains,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "ç²¾ç¥",
          width: 90,
          dataIndex: "charm",
          sorter: (a, b) => a.charm - b.charm,
          sortDirections: ['ascend', 'descend']
        },
        {
          title: "æ¶ç",
          width: 100,
          dataIndex: "gold",
          sorter: (a, b) => a.gold - b.gold,
          sortDirections: ['ascend', 'descend'],
        },
      ];
    },
    dataSourceWithKey() {
      return this.dataSource.map((d) => ({ ...d, key: genUniqueKey() }));
    },
    groupRecords() {
      const fullTimeRecords = [];
      const partTimeRecords = [];
      const gt2kRecords = [];

      for (const item of this.dataSourceWithKey) {
        if (item.workType === 1) {
          fullTimeRecords.push(item);
        } else {
          partTimeRecords.push(item);
        }
        if (item.gold >= 2000) {
          gt2kRecords.push(item);
        }
      }

      return {
        fullTimeRecords,
        partTimeRecords,
        gt2kRecords,
      };
    },
  },
  methods: {
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys;
      this.selectedRows = selectedRows;
      this.$emit("input", selectedRows);
    },
    displayOptimize(careerAddr) {
      const res = /(\w{4}).+(\w{4})/.exec(careerAddr);
      return `${res[1]}....${res[2]}`;
    },
    copyAddr(rowKey) {
      const clipboard = new ClipboardJs(`#btn-${rowKey}`);
      clipboard.on("success", () => {
        this.$message.success("å¤å¶æå");
        clipboard.destroy();
      });
    },
    execFullTime() {
      this.selectedRowKeys = this.groupRecords.fullTimeRecords.map(
        (r) => r.key
      );
      this.fireWithRecords(this.groupRecords.fullTimeRecords);
    },
    execPartTime() {
      this.selectedRowKeys = this.groupRecords.partTimeRecords.map(
        (r) => r.key
      );
      this.fireWithRecords(this.groupRecords.partTimeRecords);
    },
    exec2k() {
      this.selectedRowKeys = this.groupRecords.gt2kRecords.map((r) => r.key);
      this.fireWithRecords(this.groupRecords.gt2kRecords);
    },
    execAll() {
      this.selectedRowKeys = this.dataSourceWithKey.map((d) => d.key);
      this.fireWithRecords(this.dataSourceWithKey);
    },
    fireWithRecords(records) {
      console.log('records:', records);
      this.$emit("input", records);
      this.$emit("fire");
    },
  },
};
</script>

<style scoped>
.flex-center {
  display: flex;
  align-items: center;
}
</style>
