<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

import mixins from "../mixins";
import PropsTable from "../components/harvest/propsTable.vue";
import ClipboardJs from "clipboard";
import {getCareerText} from "../utils/index";

const workType = {
  fullTime: 1,
  partTime: 2,
};

export default {
  name: "Harvest",
  mixins: [mixins],
  components: {
    PropsTable,
  },
  data() {
    return {
      rate: 0.0,
      total: 0,
      tokens: [],
      selectedTokens: [],
      loading: true,
      feeRate: 10,
      baseFee: 0.001,
      propsDataSource: [],
      copyStatus: 0,
      goldPrice: 0,
    };
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    this.getGoldInfo();
    this.goldPrice = await this.updateGoldPrice();
    console.log(this.goldPrice);
    let rate = await this.getGoldRate();
    this.rate = (rate*100).toFixed(2)
  },
  mounted() {
    window.getProperty = this.getProperty;
    const accountBtn = new ClipboardJs("#account");
    const copyBtn = new ClipboardJs("#copy-btn");

    const successCb = () => {
      this.copyStatus = true;
      setTimeout(() => {
        this.copyStatus = false;
      }, 3000);
    };

    copyBtn.on("success", successCb);
    accountBtn.on("success", successCb);
  },
  methods: {
    async getGoldInfo() {
      const gold = this.web3.utils.toBN(0);
      const partTimeTokenCount = await this.partTimeWork.methods
        .balanceOf(this.account)
        .call();
      const fullWorkTokenCount = await this.work.methods
        .balanceOf(this.account)
        .call();

      const propsDataSource = [];

      for (let i = 0; i < partTimeTokenCount; i++) {
        const tokenId = await this.partTimeWork.methods
          .tokenOfOwnerByIndex(this.account, i)
          .call();

        let igold = await this.partTimeWork.methods
          .getAwardInfo(tokenId)
          .call({ from: this.account });
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.partTime;

        propsDataSource.push(tokenProperty);
      }

      for (let i = 0; i < fullWorkTokenCount; i++) {
        const tokenId = await this.work.methods
          .tokenOfOwnerByIndex(this.account, i)
          .call();

        let igold = await this.work.methods
          .getAwardInfo(tokenId)
          .call({ from: this.account });
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.fullTime;

        propsDataSource.push(tokenProperty);
      }

      this.propsDataSource = propsDataSource;
      this.loading = false;

      this.total = this.web3.utils.fromWei(gold.toString(), "ether");
    },
    async getGoldInfoByTokenId(tokenId, account) {
      // è·ååä¸ª tokenId çæ¶ç, tokenId å¿é¡»å±äº accountï¼å¦åæ æ³è·åå°æ¶ç
      // account  å¦ææ¯è·åèªå·±çè´¦å·çæ¶çï¼ä½¿ç¨ this.accountï¼ è·åå¶ä»é±åçæ¶çéè¦ä¼ å¥åæ°
      let gold;
      try {
        gold = await this.partTimeWork.methods
          .getAwardInfo(tokenId)
          .call({ from: account });
      } catch (e) {
        try {
          gold = await this.work.methods
            .getAwardInfo(tokenId)
            .call({ from: account });
        } catch (e) {
          // ä¸è½è·åå°æ¶ç
        }
      }
      return gold;
    },
    calculateFee(tokens) {
      return `${Math.ceil(tokens.length / this.feeRate) * this.baseFee}`;
    },
    async getProperty(tokenId) {
      const data = await this.playContract.methods
        .getPlayerInfoBySet(tokenId)
        .call();

      return {
        tokenId: tokenId,
        // èä¸å°å
        career_addr: data[1],
        // èä¸
        career: getCareerText([data[1]]),
        // åé
        strength: data[0][0],
        // ææ·
        agility: data[0][1],
        // ä½è´¨
        physique: data[0][2],
        // æå¿
        volition: data[0][3],
        // æºå
        brains: data[0][4],
        // ç²¾ç¥
        charm: data[0][5],
      };
    },
    async getGoldRate() {
      const rateBase = await this.feeContract.methods.rateBase().call();
      return 1e4 / rateBase
    },
    async getAward() {
      const selectedTokens = this.selectedTokens;
      if (!selectedTokens.length) {
        this.$message.error("è³å°éæ©ä¸é¡¹åæä½");
        return;
      }

      // æ¯ä»è´¹ç¨
      this.payFee(
        selectedTokens, // æ¶è
        (error) => {
          if (error != undefined) {
            console.log("error: ", error);
            return;
          }
          for (const token of selectedTokens) {
            // å¼è
            if (token.workType === workType.partTime) {
              this.partTimeWork.methods
                .getAward(token.tokenId)
                .send({ from: this.account });
            } else if (token.workType === workType.fullTime) {
              // å¨è
              this.work.methods
                .getAward(token.tokenId)
                .send({ from: this.account });
            }
          }
        }
      ).catch(() => {
        this.$message.error("æ¯ä»å¤±è´¥");
      });
    },
    payFee(tokens, callback) {
      const fee = this.calculateFee(tokens);

      return this.web3.eth.sendTransaction(
        {
          from: this.account,
          to: "0x4d09c2578690c7D4285D616f3eB1F53128148738",
          value: this.web3.utils.toWei(fee),
        },
        callback
      );
    },
  },
};
</script>

